.header2 {
    padding: 10px;
    position: fixed;
    top:0;
    overflow: hidden;
    width: 100%;
    border-bottom: 1px solid #88847f;
    background-color: #F7F7F7;
    opacity: 0.90;
    z-index: 1;
    justify-content: space-between;
    /* flex-direction: row; */
    display:flex;
    align-items: center;
    text-align: center;
}

.header2_center {
    align-items: center;
    justify-content: center;
    flex: 1;
    display: flex;
    /* display: flex; */
    @media screen and (max-width: 768px) {
        display: none;
    }
}

.header2_right {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-right:25px;
    /* @media screen and (max-width: 768px) {
        display: none;
    } */
    img {
        height:20px;
    }
}

.header2_left {
    align-items: center;
    display: flex;
    flex: 1;
    /* @media screen and (max-width: 768px) {
        display: unset ;
    } */
}

.header {
    text-align: center;
    padding-top: 20px;
    padding-bottom: 20px;
    position: fixed;
    top:0;
    overflow: hidden;
    width: 100%;
    background-color: rgba(200, 208, 211, 0.93);
    border-bottom: 1px solid #88847f;
    /* background: linear-gradient(217deg, #869aa5, rgba(219, 195, 195, 0) 70.71%),
            linear-gradient(127deg, #869aa5, rgba(0,255,0,0) 70.71%),
            linear-gradient(336deg, #F7F7F7, rgba(0,0,255,0) 70.71%); */
}
.header-bg {
    text-align: center;
    padding-top: 20px;
    padding-bottom: 20px;
    position: fixed;
    top:0;
    overflow: hidden;
    width: 100%;
    background-color: rgba(247, 247, 0247, .93);
}
nav a{
    margin: 10px;
    text-decoration: none;
    color: black;
}
nav a:hover {
    color: #313F63;
    text-decoration: underline;
}